module.exports = ".headings {\n  margin-top: 5px;\n  margin-bottom: 5px;\n  font-size: 14px;\n}\n\nion-input, ion-textarea {\n  font-size: 13px;\n}\n\n.input-border {\n  border: 1px solid #ccc;\n  border-radius: 5px;\n}\n\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 51px;\n  height: 24px;\n  top: 10px;\n}\n\n.switch input {\n  opacity: 0;\n  width: 0;\n  height: 0;\n}\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  -webkit-transition: 0.4s;\n  transition: 0.4s;\n}\n\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 16px;\n  width: 16px;\n  left: 4px;\n  bottom: 4px;\n  background-color: white;\n  -webkit-transition: 0.4s;\n  transition: 0.4s;\n}\n\ninput:checked + .slider {\n  background-color: #1CBCB4;\n}\n\ninput:focus + .slider {\n  box-shadow: 0 0 1px #1CBCB4;\n}\n\ninput:checked + .slider:before {\n  -webkit-transform: translateX(26px);\n  transform: translateX(26px);\n}\n\n/* Rounded sliders */\n\n.slider.round {\n  border-radius: 34px;\n}\n\n.slider.round:before {\n  border-radius: 50%;\n}\n\n.defalut-address p {\n  text-transform: capitalize;\n}\n\n.location-btn {\n  text-align: center;\n}\n\n@media screen and (min-width: 700px) {\n  #grid-content {\n    font-size: 25px;\n  }\n}\n\n@media screen and (min-width: 1000px) {\n  #grid-content {\n    font-size: 25px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"